import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "styled-components"
import posed from "react-pose"
import { Parallax, ParallaxLayer } from 'react-spring/renderprops-addons'

import SEO from "../components/seo"

import "../styles/global.css"

const Container = styled.div`
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr;
  background-color: #0A5640;
`
const BigTitle = styled.div`
grid-column: 2/12;
font-family: 'DrukCond-Super';
font-size: 100px;
color: #E9AFB8;
line-height: 0.8;
margin-top: 80px;

@media (min-width: 992px) {
  line-height: 260px;
  grid-column: 2/12;
  font-size: 320px;
  margin-top: 80px;
}
`
const Paragraph = styled.div`
    grid-column: 1/13;
    display: grid;
    grid-template-columns: repeat(12,1fr);
    grid-template-rows: repeat(2,auto);
    grid-row-gap: 35px;
    font-family: 'GT-America';
    font-size: 16px;
    color: #E9AFB8;
    margin-top: 50px;

    @media (min-width: 992px) {
    grid-column: 2/12;
    display: flex;
    flex-direction: row;
    font-size: 32px;
    }
`
const P = styled.p`
    margin: 0;
    grid-column: 2/12;
    padding-right: 0;

    @media (min-width: 992px) {
      padding-right: 50px;
      }
`
const PRight = styled.p`
    margin: 0;
    order: 2;
    flex-basis: 400%;
    grid-column: 2/12;
    padding-left: 0px;

    @media (min-width: 992px) {
      padding-left: 50px;
      }
`
const Button = styled.a`
  grid-column: 2/12;
  font-family: 'Druk-Wide-Medium';
  font-size: 16px;
  margin: 30px 0 80px 0;
  background-color: #E9AFB8;
  color: #0A5640;
  text-align: center;
  padding: 30px 0;

  @media (min-width: 992px) {
    grid-column: 3/11;
    font-size: 22px;
    margin: 50px 0 50px 0;
    padding: 50px 0;
}
`
const StyledImg = styled(Img)`
  flex-basis: 200%; 
  margin-top: 50px;
  grid-column: 1/13;

  @media (min-width: 992px) {
    margin-top: 50px;
    grid-column: 2/7;
  }
`
const Spacer = styled.div`
    height: 100px;
    

    @media (min-width: 992px) {
      height: 0px;
    }
`

// Use `posed.div` elements anywhere on the pages.

const Transition = posed.div({
  preEnter: {
      x: '100%',
      transition: {
          duration: 600,
          ease: 'easeInOut'
          }
  },
  enter: {
    x: '0%',
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
  exit: {
    x: '-100%',
    transition: {
      duration: 600,
      ease: 'easeInOut'
      }
  },
})


// ...

// const ParallaxImg = (props) => (
//     <ParallaxLayer style={{height: 0,position: 'relative', flexBasis: '200%', gridColumn: '1/13'}} offset={0} speed={0.1}>
//     <Img fluid={props.fluid}/>
//     </ParallaxLayer>
// )

const About = (props) => (
  <>
    {/* <Parallax> */}
    <Transition style={{overflow: 'scroll', height:'100vh'}}>
    <Container>
    <SEO title="About" keywords={[`gemeindehaus`, `workspace`, `coworking`]} />
    <BigTitle>
    MIT GOTTES SEGEN UND WLAN
    </BigTitle>

    <Paragraph>
        <P>
        Du suchst einen Ort, an dem Du arbeiten kannst, ohne alleine zu sein? Geselligkeit und Ruhe zugleich? Die perfekte Mischung aus Zweck-WG und Cuddle Party? Dann bist Du bei uns goldrichtig! 
        </P>
        <Img style={{flexBasis: '200%', gridColumn: '1/13'}} fluid={props.data.imageOne.childImageSharp.fluid}/>
        {/* <ParallaxImg fluid={props.data.imageOne.childImageSharp.fluid}/> */}
    </Paragraph>

    <StyledImg fluid={props.data.imageTwo.childImageSharp.fluid} />

    <Paragraph>
        <PRight>
        Wir finden gemeinsam ist alles schöner! Deswegen haben wir das Gemeindehaus zu einem Ort der Zusammenkunft gemacht. Hier tut jeder, was er am besten kann, wofür er brennt, was er liebt! Und hier kommt jeder rein, der nicht so gern in einem miefigen, anonymen Großraumbüro oder mutterseelenallein zuhause auf seinem Fjällberget Drehstuhl vor sich hin ackert. Im Gemeindehaus findet man alles, was zu einem produktiven und erfüllenden Arbeitstag gehört. Ob kreative Ekstase oder kontemplative Stille, Konzentration oder Zerstreuung, Sekt oder Selters – dieser Ort ist so vielseitig und bunt wie jeder einzelne, der hier schafft und zugleich genauso einzigartig.
        </PRight>
        <Img style={{flexBasis: '200%', gridColumn: '1/13'}} fluid={props.data.imageThree.childImageSharp.fluid} />
    </Paragraph>

    <Paragraph>
        <P>
        Alles kann, nichts muss. Aber: Hier ist alles möglich. Und: Sharing is caring! Doch wir teilen natürlich wirklich nur FAST alles: Kaffee, Druckerpatronen, Müsli, Grundwasser, saubere Einfälle und schmutzige Gedanken, Schnapsideen und auch kongeniale, die lauten und die leisen Momente, außerdem so manche Epiphanie, kein Scheiß. Gebetet wird dafür eher selten. Perish the thought!
        </P>
        <Img style={{flexBasis: '500%', gridColumn: '1/13'}} fluid={props.data.imageFour.childImageSharp.fluid} />
    </Paragraph>

    <StyledImg fluid={props.data.imageFive.childImageSharp.fluid} />

    <Button href="mailto:hi@gemeindehaus.work">
    WERDE TEIL DES GEMEINDEHAUSES <br/>
    UND SICHERE DIR DEINEN WORKSPACE
    </Button>

    <Spacer/>
    </Container>
    </Transition>
    {/* </Parallax> */}
  </>
)

export default About

export const pageQuery = graphql`
  query {
    imageOne: file(relativePath: { eq: "about/about12.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    imageTwo: file(relativePath: { eq: "about/about22.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    imageThree: file(relativePath: { eq: "about/about32.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    imageFour: file(relativePath: { eq: "about/about42.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    imageFive: file(relativePath: { eq: "about/about52.jpg" }) {
      childImageSharp {
        fluid(maxWidth: 1000) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`